import { CompoundInterestCalculatorInput } from "../../models/compound-interest-calculator-input";
import { CompoundInterestCalculatorOutput } from "../../models/compound-interest-calculator-output";
import { CalculatorService } from "./calculator-service";
import { injectable, inject } from "inversify-props";
import { CompoundInterestCalculator } from '../../../../core/compound-interest-calculator';
import CoreSymbols from "../../../../core/symbols";

@injectable()
export class CalculatorServiceImpl implements CalculatorService {
  private calculator: CompoundInterestCalculator;

  constructor (@inject(CoreSymbols.CompoundInterestCalculator) calculator: CompoundInterestCalculator) {
    this.calculator = calculator;
  }

  public calculateCompoundInterest (input: CompoundInterestCalculatorInput): CompoundInterestCalculatorOutput {
    return this.calculator.Calculate(input);
  }
}