import { Container, getContainer } from 'inversify-props';
import Symbols from './symbols';
import { CompoundInterestCalculator, CompoundInterestCalculatorImpl } from './compound-interest-calculator';

export class CompoundInterestCalculatorBinder {
  public static with (container: Container = getContainer()) {
    container.bind<CompoundInterestCalculator>(Symbols.CompoundInterestCalculator).to(CompoundInterestCalculatorImpl).inTransientScope();
  }
}