import { CompoundInterestCalculatorInput } from "@/components/compound-interest-calculator/models/compound-interest-calculator-input";
import { CompoundInterestCalculatorOutput, CalculateCompoundInterestDetail } from "@/components/compound-interest-calculator/models/compound-interest-calculator-output";
import { CompoundInterestCalculator } from "./compound-interest-calculator";
import { PeriodType, InterestType } from '../../components/compound-interest-calculator/models/compound-interest-calculator-input';
import { injectable } from "inversify-props";

@injectable()
export class CompoundInterestCalculatorImpl implements CompoundInterestCalculator {
  private hasInitialContribution = false;
  private periodInMonths = 0;
  private interestPercentageInMonths = 0;
  private totalInvested = 0;

  public Calculate (input: CompoundInterestCalculatorInput): CompoundInterestCalculatorOutput {
    this.SetProperties(input);

    const periodLength = this.hasInitialContribution
      ? this.periodInMonths + 1
      : this.periodInMonths;

    let totalWithInterest = 0;
    const details: CalculateCompoundInterestDetail[] = [];

    for (let index = 0; index < periodLength; index++) {
      if (index === 0 && this.hasInitialContribution) {
        totalWithInterest += input.initialContribution!;

        details.push(
          new CalculateCompoundInterestDetailImpl(
            index,
            0,
            totalWithInterest,
            0,
            totalWithInterest,
            true
          )
        );

        continue;
      }

      const interest = totalWithInterest * (this.interestPercentageInMonths / 100);
      totalWithInterest += (input.monthlyValue ?? 0) + (totalWithInterest * (this.interestPercentageInMonths ?? 0) / 100);
      const totalInvestedSoFar = (input.monthlyValue ?? 0) * (index + 1);

      details.push(
        new CalculateCompoundInterestDetailImpl(
          index,
          interest,
          totalInvestedSoFar,
          totalWithInterest - totalInvestedSoFar,
          totalWithInterest,
          false
        )
      );
    }

    const interestTotal = totalWithInterest - this.totalInvested;

    return new CompoundInterestCalculatorOutputImpl(
      totalWithInterest,
      this.totalInvested,
      interestTotal,
      details
    );
  }

  private SetProperties (input: CompoundInterestCalculatorInput): void {
    this.interestPercentageInMonths = input.interestType == InterestType.monthly
      ? input.interestPercentage
      : input.interestPercentage / 12;

    this.hasInitialContribution =
      input.initialContribution !== undefined
      && input.initialContribution !== null
      && input.initialContribution !== 0;

    this.periodInMonths =
      input.periodType == PeriodType.monthly
        ? input.period
        : input.period * 12;

    this.totalInvested = this.hasInitialContribution
      ? input.initialContribution! + ((input.monthlyValue ?? 0) * this.periodInMonths)
      : input.monthlyValue ?? 0 * this.periodInMonths;
  }
}

class CalculateCompoundInterestDetailImpl implements CalculateCompoundInterestDetail {
  constructor (
    month: number,
    interest: number,
    totalInvested: number,
    totalInterest: number,
    accumulatedTotal: number,
    initialContribution: boolean
  ) {
    this.month = month;
    this.interest = interest;
    this.totalInvested = totalInvested;
    this.totalInterest = totalInterest;
    this.accumulatedTotal = accumulatedTotal;
    this.initialContribution = initialContribution;
  }

  public month: number;
  public interest: number;
  public totalInvested: number;
  public totalInterest: number;
  public accumulatedTotal: number;
  public initialContribution: boolean;
}

class CompoundInterestCalculatorOutputImpl implements CompoundInterestCalculatorOutput {
  constructor (
    finalTotalValue: number,
    totalAmountInvested: number,
    totalInInterest: number,
    details: CalculateCompoundInterestDetail[]
  ) {
    this.finalTotalValue = finalTotalValue;
    this.totalAmountInvested = totalAmountInvested;
    this.totalInInterest = totalInInterest;
    this.details = details;
  }

  public finalTotalValue: number;
  public totalAmountInvested: number;
  public totalInInterest: number;
  public details: CalculateCompoundInterestDetail[];
}